<template>
    <div>
        <el-row type="flex" justify="center">
            <el-col :span="18">
            <el-form v-model="currentUser" ref="dataForm">
                <el-form-item prop="userface">
                    <el-avatar 
                        style="color: #222; margin-top: 20px"
                        :src="currentUser.userface"
                        :size="150">
                    </el-avatar>
                </el-form-item>
                <el-form-item>
                    <el-upload
                        class="img-upload"
                        ref="upload"
                        action=""
                        with-credentials
                        :on-preview="handlePreview"
                        :on-remove="handleRemove"
                        :before-remove="beforeRemove"
                        :on-success="handleSuccess"
                        :on-error="handleError"
                        multiple
                        :limit="1"
                        :on-exceed="handleExceed"
                        :http-request="uploadHeader"
                        :disabled="widgetDisable">
                        <el-button size="small" type="primary"></el-button>
                    </el-upload>
                </el-form-item>
                <el-form-item label="" prop="name">
                    <el-input v-model="currentUser.name" :disabled="widgetDisable"></el-input>
                </el-form-item>
                <el-form-item label="ID" prop="cosmosId">
                    <el-input v-model="currentUser.cosmosId" :disabled="widgetDisable"></el-input>
                </el-form-item>
                <el-form-item label="" prop="password">
                    <el-input v-model="currentUser.password" :disabled="widgetDisable" ></el-input>
                </el-form-item>
                <el-form-item label="" prop="birthday">
                    <el-date-picker
                    v-model="currentUser.birthday"
                    align="right"
                    type="date"
                    placeholder=""
                    >
                    </el-date-picker>
                </el-form-item>
                
                <el-form-item label="" prop="intro">
                    <el-input v-model="currentUser.intro" :disabled="widgetDisable"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <label> 100 </label>
                </el-form-item>
                <el-form-item prop="id">
                    <label>id: {{ currentUser.id }}</label>
                </el-form-item>
            </el-form>
            <el-button @click="enterEditStatus()">{{ buttonText }}</el-button>
            </el-col>
        </el-row>

    </div>
</template>

<script>
import { apiGetUserInfo } from "@/api/user"
import { apiUpdateUserInfo } from "@/api/user"
import { uploadImageToServer } from "@/api/user"

export default {
    name: 'Profile',
    data() {
        return {
            widgetDisable: true,
            currentUser: {
                id: 1,
                userface: "",
                name: "noname",
                cosmosId: "9909",
                password: "12333",
                intro: "",
                birthday: "1994-01-22"
            },
            buttonText: '',
        }
    },
    methods: {
        fetchUserInfo() {
            apiGetUserInfo().then(res => {
                console.log("profile :get : res:", res)
                this.currentUser = res.data.obj
            })
        },
        enterEditStatus() {
            // it was previously disabled, now false disable
            if (this.widgetDisable == true) {
                // want to update
                this.widgetDisable = false
                this.buttonText = ''
            } else {
                // want to submit
                apiUpdateUserInfo(this.currentUser).then(res => {
                    console.log("after api update user, res: ", res)
                    if (res.data.status == 200) {
                        this.$store.commit('SET_USER_COSMOS_ID', this.currentUser.cosmosId);
                        // ok 
                        this.$message({
                            type: "success",
                            message: ""
                        })
                        this.widgetDisable = true
                        this.buttonText = ''
                        this.fetchUserInfo()
                    } else {
                        this.$message({
                            type: "warning",
                            message: ""
                        })
                    }
                })
            }
            
        },
        handleRemove (file, fileList) {
            console.log(file, fileList)
        },
        handlePreview (file) {
            console.log(file)
            //  file 
            // console.log(file.response)
        },
        handleExceed (files, fileList) {
            this.$message({type: "warning",
                        message: ` 1  ${files.length}  ${files.length + fileList.length} `
                        })
        },
        beforeRemove (file, fileList) {
            return this.$message(` ${file.name}`)
        },
        handleSuccess (response) {
            this.url = response
            console.log("img uploaded to" , this.url)
            this.$emit('onUpload')
            this.$message('')
        },
        handleError(response) {
            console.log("err: ", response)
        },
        clear () {
            this.$refs.upload.clearFiles()
        },
        uploadHeader(params) {
            var that = this,
            file = params.file,
            fileType = file.type,
            isImage = fileType.indexOf('image') != -1,
            file_url = that.$refs.upload.uploadFiles[0].url;
            console.log("file is ", file)
            var isLt2M = file.size / 1024 / 1024 < 2;
            if (!isLt2M) {
                alert("2M");
                that.$refs.upload.uploadFiles=[];
                return;
            }
            if (!isImage) {
                alert("");
                that.$refs.upload.uploadFiles=[];
                return;
            }
            if (isImage) {
            console.log("ready to upload header")
            var formData = new FormData();
            formData.append("file", file)
            uploadImageToServer(formData).then((res)=>{
                console.log("server resp: ", res)
                /*
                config:Object
                data:"http://101.43.166.211:8081/api/img/offj5o.jpg"
                headers:Object
                request:XMLHttpRequest
                status:200
                statusText:""
                */
                this.currentUser.userface = res.data
                that.$emit('onUpload')
                that.$message('')
            })
            
            }
        }
    },
    mounted() {
        apiGetUserInfo().then(res => {
                console.log("profile :get : res:", res)
                this.currentUser = res.data.obj
            })
    }
}
</script>


